/*screen1*/
.screen1 {
  height: 680px;
  background: url(../images/top.jpg) no-repeat top center;
  background-size: cover;
  position: relative;
  padding-top: 85px;
  box-sizing: border-box;
}

.screen1-title {
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  font-weight: 100;
  font-size: 5rem;
  line-height: 4.5rem;
  color: #fff;
  font-style: normal;
  margin-bottom: 2rem;
}

.screen1-slogan {
  color: #fff;
  font-size: 1.5rem;
  line-height: 2rem;
  font-family: "Cambria", Arial, Helvetica, sans-serif;
  max-width: 525px;
  margin-bottom: 2.8rem;
}

.screen1-slogan._black {
  color: #000;
  max-width: 100%;
}

.screen1-slogan--center {
  max-width: 820px;
  margin-left: auto;
  margin-right: auto;
}

.screen1-slogan--center2 {
  max-width: 1030px;
  margin-left: auto;
  margin-right: auto;
}

.screen1.show .monitors .monitor1 {
  opacity: 1;
  transform: translate(0, 0);
}

.screen1.show .monitors .monitor2 {
  opacity: 1;
  transform: translate(0, 0);
}

.screen1.show .monitors .graf1 {
  opacity: 1;
  transform: translate(0, 0);
}

.screen1.show .monitors .graf2 {
  opacity: 1;
  transform: translate(0, 0);
}

.screen1.show .monitors .graf3 {
  opacity: 1;
  transform: translate(0, 0);
}

.left-size {
  max-width: 715px;
  width: 55%;
}

.logo {
  line-height: 0;
  display: block;
  padding-bottom: 70px;
  position: relative;
  margin-bottom: 25px;
}

.logo:after {
  position: absolute;
  content: '';
  bottom: 0;
  left: 0;
  height: 2px;
  width: 100px;
  background: #fff;
}

.monitors {
  position: absolute;
  right: 0;
  top: 0;
  width: 52.5%;
}

.monitors .monitor1 {
  left: 0;
  top: 200px;
  position: absolute;
  line-height: 0;
  max-width: 44%;
  opacity: 0;
  transform: translate(50%, 0);
  transition: all 0.5s 0.4s ease;
}

.monitors .monitor2 {
  right: 0;
  top: 0;
  position: absolute;
  line-height: 0;
  max-width: 65%;
  opacity: 0;
  transform: translate(50%, 0);
  transition: all 0.5s ease;
}

.monitors .graf1 {
  position: absolute;
  right: 45%;
  top: 130px;
  line-height: 0;
  max-width: 33%;
  opacity: 0;
  transform: translate(50%, 0);
  transition: all 0.5s 0.7s ease;
}

.monitors .graf2 {
  position: absolute;
  right: 0;
  top: 150px;
  line-height: 0;
  max-width: 40%;
  opacity: 0;
  transform: translate(50%, 0);
  transition: all 0.5s 1s ease;
}

.monitors .graf3 {
  position: absolute;
  right: 30%;
  top: 280px;
  line-height: 0;
  max-width: 41%;
  opacity: 0;
  transform: translate(50%, 0);
  transition: all 0.5s 1.3s ease;
}

/*screen2*/
.screen2 {
  padding: 100px 0 80px;
  box-sizing: border-box;
}

.block-with-underground {
  position: relative;
  padding-top: 60px;
}

.about {
  margin-bottom: 70px;
}

.about-block {
  position: relative;
  width: 100%;
  height: 220px;
  border: 1px solid #e7e8eb;
  box-sizing: border-box;
  border-radius: 7px;
  line-height: 220px;
  margin-bottom: 20px;
  text-align: center;
  transition: box-shadow 0.3s ease;
  background: #fff;
  transition: transform 0.3s ease, opacity 0.3s ease;
}

.about-block--big {
  height: 470px;
  line-height: 470px;
}

.about-block--green {
  border: 1px solid #8aba64;
  background: #8aba64;
}

.about-block--green .about-text {
  color: #fff;
  max-width: 240px;
}

.about-block--gray {
  border: 1px solid #e7e8eb;
  background: #e7e8eb url(../images/graf.png) no-repeat 40px 15px;
  text-align: right;
}

.about-block--gray .about-text {
  max-width: 230px;
  text-align: center;
  margin-right: 10%;
}

.about-block--fiol {
  background: url(../images/fiol.jpg) no-repeat top center;
  border: none;
  text-align: left;
}

.about-block--fiol .about-text {
  max-width: 230px;
  text-align: center;
  margin-left: 10%;
}

.about-block--hulf + .about-block--hulf {
  margin-top: 30px;
}

.about-block:hover {
  box-shadow: 0 30px 70px rgba(0, 0, 0, 0.4);
  z-index: 2;
}

.about-block--cur {
  box-shadow: 0 30px 70px rgba(0, 0, 0, 0.4);
  z-index: 2;
}

.about-block--cur:hover {
  box-shadow: none;
}

.about-text {
  display: inline-block;
  vertical-align: middle;
  color: #000;
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: bold;
  max-width: 300px;
}

.about-text--roboto {
  font-weight: lighter;
  display: block;
  font-size: 1rem;
  margin-top: 10px;
  line-height: 1.5rem;
}

.about-text--cambria {
  font-family: "Cambria", Arial, Helvetica, sans-serif;
  font-weight: normal;
}

.about-text svg {
  display: block;
  margin: 0 auto 5px;
  max-width: 54px;
  max-height: 54px;
  fill: #8aba64;
}

.about-text--big {
  color: #fff;
  font-weight: bold;
  font-size: 1.5rem;
  line-height: 2rem;
  margin-bottom: 20px;
}

.about-text--big span {
  font-size: 8rem;
  display: block;
  line-height: 6rem;
}

.about-text--light {
  color: #fff;
  font-weight: lighter;
  font-size: 1.5rem;
  line-height: 2rem;
  margin-bottom: 45px;
}

.about-img--big {
  position: absolute;
  top: 40px;
  width: 440px;
  height: 440px;
  right: 0;
  background: url(../images/about.png) no-repeat top center;
}

.about-left {
  font-size: 1.5rem;
  line-height: 2rem;
  font-family: "Cambria", Arial, Helvetica, sans-serif;
  color: #000;
  margin-bottom: 25px;
}

.about-right--title {
  font-weight: bold;
  color: #000;
  font-size: 1rem;
  line-height: 1.5rem;
  margin-bottom: 30px;
}

.about-list {
  margin-bottom: 30px;
}

.about-list__item {
  margin-bottom: 20px;
}

.about-list__svg {
  float: left;
  width: 25%;
  text-align: center;
}

.about-list__svg svg {
  fill: #8aba64;
  max-width: 50px;
  max-height: 50px;
}

.about-list__text {
  overflow: hidden;
  font-size: 16px;
  line-height: 24px;
  color: #000;
  font-weight: 300;
  padding-right: 30px;
  box-sizing: border-box;
}

.anchor-link {
  cursor: pointer;
}

.about-text-list ul {
  padding: 0;
  margin: 0;
  list-style: none;
  columns: 2;
}

.about-text-list ul li {
  display: inline-block;
  vertical-align: top;
  padding-left: 45px;
  box-sizing: border-box;
  position: relative;
  margin-bottom: 30px;
  font-size: 14px;
  line-height: 22px;
  font-weight: normal;
  font-style: normal;
}

.about-text-list ul li:after {
  position: absolute;
  content: '';
  width: 29px;
  height: 16px;
  top: 0;
  left: 0;
  background: url(data:image/png;base64,) no-repeat top center;
}

/*screen3*/
.screen3 {
  padding: 60px 0;
  box-sizing: border-box;
  background: #7e2aa9 url(../css/pic/fiol.jpg) no-repeat top center;
  background-size: cover;
}

.portfolio {
  margin-bottom: 15px;
}

.portfolio .gcell {
  margin-bottom: 30px;
}

.portfolio-item {
  line-height: 0;
  display: block;
  position: relative;
  transition: all 0.3s ease;
}

.portfolio-item:after {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(138, 186, 100, 0.9);
  opacity: 0;
  transition: all 0.3s ease;
}

.portfolio-item:hover {
  box-shadow: 0 40px 60px rgba(0, 0, 0, 0.4);
  z-index: 2;
}

.portfolio-item:hover:after {
  opacity: 1;
}

.portfolio-item:hover .portfolio-link {
  opacity: 1;
  transition: all 0.3s 0.2s ease;
}

.portfolio-link {
  z-index: 2;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
  display: inline-block;
  vertical-align: top;
  text-align: center;
  max-width: 90%;
  opacity: 0;
}

.portfolio-link svg {
  fill: #fff;
  height: 66px;
  width: 66px;
  margin-bottom: 15px;
}

.portfolio-link span {
  color: #fff;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  font-weight: lighter;
  font-size: 1.2rem;
  line-height: 1.5rem;
  display: block;
}

.touchevents .portfolio {
  margin-bottom: 15px;
}

.touchevents .portfolio .gcell {
  margin-bottom: 30px;
}

.touchevents .portfolio-item {
  line-height: 0;
  display: block;
  position: relative;
  transition: all 0.3s ease;
}

.touchevents .portfolio-item:hover {
  box-shadow: 0 40px 60px rgba(0, 0, 0, 0.4);
  z-index: 2;
}

.touchevents .portfolio-item:hover:after {
  opacity: 1;
}

.touchevents .portfolio-item:hover .portfolio-link {
  opacity: 1;
  transition: all 0.3s 0.2s ease;
}

.touchevents .portfolio-link {
  z-index: 2;
  top: auto;
  bottom: 0;
  left: 0%;
  transform: translate(0%, 0%);
  position: absolute;
  display: block;
  text-align: center;
  width: 100%;
  max-width: none;
  opacity: 1;
}

.touchevents .portfolio-link svg {
  display: none;
}

.touchevents .portfolio-link span {
  color: #fff;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  font-weight: 400;
  text-shadow: 1px 1px 1px #000;
  font-size: 1.2rem;
  line-height: 1.5rem;
  display: block;
  text-align: center;
  position: relative;
  z-index: 99999;
  padding: 5px 0;
}

.touchevents .portfolio-link:after {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(138, 186, 100, 0.9);
  opacity: 1;
  transition: all 0.3s ease;
}

.add-new {
  left: 0;
  height: 100%;
  width: 100%;
  border: 2px dashed rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
  position: absolute;
}

.add-new__text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 1rem;
  line-height: 1.5rem;
}

.add-new__text span {
  display: block;
  font-size: 6rem;
  line-height: 5rem;
}

.add-new:hover {
  border: 2px dashed white;
}

.add-new:hover .add-new__text {
  color: white;
}

